


















import OrderList from '../../components/orderList/orderList.vue'
export default{
	components:{
		OrderList
	},
	data(){
		return{
			top:[
				{name:'全部',status:100},
				{name:'已成团',status:1},
				{name:'确认收货',status:2},
				{name:'已经结算',status:5}
				],
				tabIndex:0,
				Maindata:[
				],
				tomName:'全部',
				topStatus:100
		}
	},
	methods:{
		orderIndex(i,n){
			this.tabIndex=n;
			this.topStatus=i.status;
		}
	},
	onLoad:function(e){
		// console.log("页面传值:",e);
		uni.showLoading({
			title:'努力加载中...'
		})
		uni.setNavigationBarTitle({
			title:e.name
		})
		uni.hideShareMenu();
		// 商品请求
		uni.request({
			url: e.quest,
			method: 'GET',
			data: {
				openid:e.get
			},
			success: res => {
				// console.log("order数据状态:",res);
				this.Maindata=res.data.data.my_order;
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		});
	}
}
